import React, { useEffect, useState } from "react";
import Filter from "./components/Filter";
import PersonForm from "./components/PersonForm";
import Persons from "./components/Persons";
import phoneService from "./services/phonebook";
import { Grid, makeStyles } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.secondary.main,
    height: "100vh",
    // backgroundColor: theme.palette.secondary.main,
  },
}));

const App = () => {
  const classes = useStyles();
  const [persons, setPersons] = useState([]);
  const [newName, setNewName] = useState("");
  const [newNumber, setNewNumber] = useState("");
  const [newFilter, setNewFilter] = useState("");

  const handleNameChange = (event) => setNewName(event.target.value);
  const handleNumberChange = (event) => setNewNumber(event.target.value);
  const handleFilterChange = (event) => setNewFilter(event.target.value);

  useEffect(() => {
    phoneService.getAll().then((response) => {
      console.log(response);
      setPersons(response);
    });
  }, []);

  const handleDeleteClick = (person) => {
    if (!window.confirm(`Delete ${person.name}?`)) return;
    phoneService.remove(person.id).then(() => {
      setPersons(persons.filter((people) => people.id !== person.id));
    });
  };

  const addPerson = (event) => {
    event.preventDefault();
    const newPerson = { name: newName, number: newNumber };
    const duplicateName = persons.find((person) =>
      person.name.includes(newPerson.name)
    );

    if (
      duplicateName &&
      window.confirm(
        `${newPerson.name} is already in the phonebook, replace the old number with a new one?`
      )
    ) {
      phoneService
        .update(duplicateName.id, newPerson)
        .then((returnedPerson) => {
          setPersons(
            persons.map((person) =>
              person.id !== duplicateName.id ? person : returnedPerson
            )
          );
        });
    } else {
      phoneService
        .create(newPerson)
        .then((returnedPerson) => setPersons(persons.concat(returnedPerson)));
    }
    setNewName("");
    setNewNumber("");
  };

  const personsToShow =
    persons.length === 0
      ? persons
      : persons.filter((person) =>
          person.name.toLowerCase().includes(newFilter.toLowerCase())
        );

  return (
    <div className={classes.root}>
      <Grid
        container
        direction="column"
        justifyContent="center"
        alignItems="center"
        xs={12}
      >
        <h1>Phonebook</h1>
        <Filter newFilter={newFilter} handleFilterChange={handleFilterChange} />
        <h2>Add a new Number</h2>
        <PersonForm
          addPerson={addPerson}
          handleNameChange={handleNameChange}
          handleNumberChange={handleNumberChange}
          newName={newName}
          newNumber={newNumber}
        />
        <h2>Numbers</h2>
        <Persons
          persons={personsToShow}
          handleDeleteClick={handleDeleteClick}
        />
      </Grid>
    </div>
  );
};

export default App;
