import React from "react";
import {
  Button,
  IconButton,
  InputAdornment,
  TextField,
} from "@material-ui/core";
import PersonAddIcon from "@material-ui/icons/PersonAdd";
import PhoneIcon from "@material-ui/icons/Phone";
import TextFieldsIcon from "@material-ui/icons/TextFields";

const PersonForm = ({
  addPerson,
  newName,
  handleNameChange,
  newNumber,
  handleNumberChange,
}) => {
  return (
    <div>
      <form onSubmit={addPerson}>
        <div>
          <TextField
            id="filled-basic"
            label="Name:"
            variant="filled"
            value={newName}
            onChange={handleNameChange}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <IconButton>
                    <TextFieldsIcon />
                  </IconButton>
                </InputAdornment>
              ),
            }}
          />
          <div>
            <TextField
              id="filled-basic"
              label="Number:"
              variant="filled"
              value={newNumber}
              onChange={handleNumberChange}
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <IconButton>
                      <PhoneIcon />
                    </IconButton>
                  </InputAdornment>
                ),
              }}
            />
          </div>
          <div>
            <Button
              startIcon={<PersonAddIcon fontSize="small" />}
              size="small"
              variant="contained"
              color="primary"
              type="submit"
            >
              Add
            </Button>
          </div>
        </div>
      </form>
    </div>
  );
};

export default PersonForm;
